MPORTS ---------------------- */\r\n@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Text:wght@400;500&display=swap');\r\n@import url('https://fonts.googleapis.com/css2?family=Questrial&display=swap');\r\n@import url('https://fonts.googleapis.com/css2?family=Teko&display=swap');\r\n\r\n/*  -----------------------    MIXINS AND VARIABLES ----------------------- */\r\n\r\n@mixin flexCenter($direction){\r\n    display: flex;\r\n    flex-direction: $direction;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n$sea-color: radial-gradient(#3a78c9 30%, #336bb5);\r\n$cannonball-color: #1c1c1c;\r\n$ship-main-color: #595959;\r\n$ship-gradient-color:#828282;\r\n$container-color: #f2f2f2;\r\n$main-font: 'Big Shoulders Stencil Text', cursive;\r\n$notification-font: 'Teko', sans-serif;\r\n\r\n:root {\r\n    font-family: $main-font;\r\n}\r\n@media only screen and (max-width: 1200px) {\r\n    :root {\r\n        font-size: 0.8rem;\r\n    }\r\n}\r\n@media only screen and (max-width: 600px) {\r\n    :root {\r\n        font-size: 0.6em;\r\n    }\r\n}\r\n@media only screen and (max-width: 450px){\r\n    :root {\r\n        font-size: 0.5em;\r\n    }\r\n}\r\n\r\n\r\n/*  ----------------------  FILE IMPORTS ---------------------- */\r\n\r\n@import \"./header\";\r\n@import \"./main-container\";\r\n@import \"./ship-styling\";\r\n@import \"./hit-styling\";\r\n@import \"./waves-styling\";\r\n\r\nbody {\r\n    display: block !important;\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbutton {\r\n    cursor: pointer;\r\n}\r\n\r\n.hide {\r\n    display: none !important;\r\n}\r\n\r\n.fade {\r\n    opacity: 0.3;\r\n}\r\n\r\n#grid-container-div {\r\n    display: grid;\r\n    grid-template-rows: 13vh 87vh;\r\n}\r\n\r\n.hover-ship-div {\r\n    text-align: center;\r\n    border: 0.2rem solid black;\r\n    background: white;\r\n    opacity: 0.75;\r\n    position: absolute;\r\n    line-height: 0.5rem;\r\n    z-index: 4;\r\n    list-style-type: none;\r\n    padding: 0.3em;\r\n    margin: 0;\r\n    font-family: $notification-font;\r\n}\r\n\r\n.crossed-out {\r\n    text-decoration: line-through red;\r\n    text-decoration-thickness: 0.2rem;\r\n}\r\n\r\n/*  ----------------------  MISS STYLING ------------------- */\r\n\r\n.miss {\r\n    font-size: 1.8em;\r\n    color: red;\r\n    font-family: 'Questrial', sans-serif;    \r\n}\r\n\r\n@media screen and (max-width: 1200px){\r\n    .miss {\r\n        font-size: 1.2em;\r\n    }\r\n}\r\n\r\n.ship-sunk {\r\n    background: red;\r\n}\r\n\r\n/* ---------------------    NOTIFICATIONS    ---------------------------- */\r\n\r\n.gameover-notification {\r\n    position: relative;\r\n    left: -50%;\r\n    z-index: 5;\r\n    font-size: 15em;\r\n    font-family: $notification-font;\r\n    color: brown;\r\n    text-shadow: 0 10px 2px white;\r\n    animation: scaleUp 2s;\r\n}\r\n\r\n@keyframes scaleUp {\r\n    from {\r\n        font-size: 1em;\r\n    }\r\n    to {\r\n        font-size: 15em;\r\n    }\r\n}\r\n\r\n.gameover-container {\r\n    position: absolute;\r\n    left: 50%;\r\n    top: 15%;\r\n}\r\n\r\n#notification-box {\r\n    @include flexCenter(column);\r\n    padding: 0 1em;\r\n    margin-left: auto;\r\n    margin-right: auto;\r\n    position: relative;\r\n    background: #FFF720;\r\n    text-align: center;\r\n    border: 0.5em solid black;\r\n    border-radius: none;\r\n    z-index: 4;\r\n    left: -50%;\r\n    line-height: 3em;\r\n    h1 {\r\n        font-size: 4em;\r\n        font-family: $notification-font;\r\n        text-transform: uppercase;\r\n    }\r\n}\r\n\r\n#notification-container {\r\n    position: absolute;\r\n    left: 50%;\r\n    width: 35vw;\r\n    top: 10%;\r\n}\r\n\r\n@media screen and (max-width: 800px){\r\n    #notification-container {\r\n        width: 45vw;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 600px){\r\n    #notification-container {\r\n        width: 80%;\r\n    }\r\n}\r\n\r\n@media screen and (max-height: 550px){\r\n    #notification-container {\r\n        font-size: 0.5em;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 2000px){\r\n    #notification-box {\r\n        h2 {\r\n            font-size: 3em;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n","\r\nheader {\r\n    grid-area: 'header';\r\n    @include flexCenter(row)\r\n}\r\n\r\n#vector-credit {\r\n    position: absolute;\r\n    top: 1%;\r\n    left: 20%;\r\n}\r\n\r\n@media screen and (max-width: 1200px){\r\n    #vector-credit {\r\n        left: 1%;\r\n    }\r\n}\r\n\r\n/*  ----------------------  TITLE ------------------------*/\r\n\r\n#game-title {\r\n    text-align: center;\r\n    font-size: 5em;\r\n    opacity: 0.3;\r\n    margin-block-start: 0;\r\n    margin-block-end: 0;\r\n    z-index: 2;\r\n    color: brown;\r\n    text-shadow: 0 2px 2px white;\r\n}\r\n\r\n#behind-title-img {\r\n    background: $cannonball-color;\r\n    border-radius: 50%;\r\n    width: 7rem;\r\n    height: 7rem;\r\n    position: absolute;\r\n    box-shadow: 0 0 30px 30px lightgray;\r\n}\r\n\r\n@media screen and (max-width: 900px) and (max-height: 540px) and (orientation: landscape) {\r\n    #game-title {\r\n        font-size: 3em;\r\n    }\r\n    #behind-title-img {\r\n        display: none;\r\n    }\r\n}\r\n\r\n/*  ----------------------  SHIP IMG ------------------------*/\r\n\r\n.ship-img {\r\n    height: 15vw;\r\n    width: 15vw;\r\n    position: absolute;\r\n}\r\n\r\n@media only screen and (max-width: 1200px){\r\n    .ship-img {\r\n        height: 10em;\r\n        width: 10em;\r\n        position: absolute;\r\n    }\r\n}\r\n@media only screen and (max-width: 900px) and (max-height: 500px) and (orientation: landscape){\r\n    .ship-img {\r\n        display: none;\r\n    }\r\n}\r\n\r\n@media only screen and (max-width: 600px){\r\n    .ship-img {\r\n        height: 10em;\r\n        width: 10em;\r\n    }\r\n}\r\n@media only screen and (max-width: 450px){\r\n    .ship-img {\r\n        display: none;\r\n        }\r\n}\r\n/*  ---------------------    LEFT SIDE SHIP ANIMATION --------------------------    */\r\n\r\n#ship-1 {\r\n    left: 2%;\r\n    top: 2%;\r\n    transform: rotate(10deg);\r\n    z-index: 1;\r\n}\r\n\r\n#header-cannonball-1-container {\r\n    animation: fire-left-xaxis1 1.2s ease-in;\r\n    position: absolute;\r\n    left: 14%;\r\n    top: 10.5%;\r\n}\r\n\r\n@keyframes fire-left-xaxis1 {\r\n    from {\r\n        transform: translateX(0px);\r\n    }\r\n    to {\r\n        transform: translateX(30vw);\r\n    }\r\n}\r\n\r\n#header-cannonball-1 {\r\n    background: $cannonball-color;\r\n    border-radius: 50%;\r\n    animation: fire-left-yaxis1 1.2s ease-out;\r\n}\r\n\r\n@keyframes fire-left-yaxis1 {\r\n    from {\r\n        width: 1px;\r\n        height: 1px;\r\n        transform: translateY(0px);\r\n    }\r\n    to {\r\n        width: 1vw;\r\n        height: 1vw;\r\n        transform: translateY(-4vw)\r\n    }\r\n}\r\n\r\n/*  ---------------------    RIGHT SIDE SHIP ANIMATION   --------------------------    */\r\n\r\n#ship-2 {\r\n    right: 2%;\r\n    top: 2%;\r\n    transform: scaleX(-1) rotate(10deg);\r\n    z-index: 1;\r\n}\r\n\r\n#header-cannonball-2-container {\r\n    animation: fire-left-xaxis2 1.2s ease-in;\r\n    position: absolute;\r\n    right: 14%;\r\n    top: 10.5%;\r\n}\r\n\r\n@keyframes fire-left-xaxis2 {\r\n    from {\r\n        transform: translateX(0px);\r\n    }\r\n    to {\r\n        transform: translateX(-30vw);\r\n    }\r\n}\r\n\r\n#header-cannonball-2 {\r\n    background: #1c1c1c;\r\n    border-radius: 50%;\r\n    animation: fire-left-yaxis2 1.2s ease-out;\r\n}\r\n\r\n@keyframes fire-left-yaxis2 {\r\n    from {\r\n        width: 1px;\r\n        height: 1px;\r\n        transform: translateY(0px);\r\n    }\r\n    to {\r\n        width: 1vw;\r\n        height: 1vw;\r\n        transform: translateY(-4vw)\r\n    }\r\n}\r\n\r\n@media only screen and (max-width: 1500px){\r\n    #ship-1, #ship-2 {\r\n        top: 6%;\r\n    }\r\n}\r\n@media only screen and (max-width: 900px) and (max-height: 500px) and (orientation: landscape){\r\n    #header-cannonball-1, #header-cannonball-2 {\r\n        display: none;\r\n    }\r\n}\r\n@media only screen and (max-width: 768px){\r\n    #header-cannonball-1, #header-cannonball-2 {\r\n        display: none;\r\n    }\r\n}\r\n@media only screen and (max-width: 600px){\r\n    #ship-1, #ship-2 {\r\n        top: 5%;\r\n    }\r\n    #header-cannonball-1, #header-cannonball-2 {\r\n        display: none;\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n",".container {\r\n    display: flex;\r\n}\r\n\r\n/*  -------------------------   CONTAINERS ---------------------------  */\r\n\r\n#ai-grid-container {\r\n    flex-basis: 25%;\r\n    text-align: center;\r\n    background: linear-gradient(160deg, transparent 12.5%, $container-color 12.5%);\r\n    font-size: 1.5em;\r\n    @include flexCenter(column);\r\n}\r\n\r\n#player-grid-container {\r\n    flex-basis: 50%;\r\n    background: $container-color;\r\n    font-size: 1.5em;\r\n    @include flexCenter(column);\r\n    border-left: 0.3em solid white;\r\n    border-right: 0.3em solid white;\r\n}\r\n\r\n#ui-controls {\r\n    flex-basis: 25%;\r\n    background: linear-gradient(-160deg, transparent 12.5%, $container-color 12.5%);\r\n    position: relative;\r\n    @include flexCenter(column);\r\n}\r\n\r\n@media screen and (max-width: 1200px){\r\n    .container {\r\n        display: grid;\r\n        grid-template-columns: 1fr 1fr 1fr;\r\n        grid-template-areas: \r\n        \"player-grid player-grid ui-controls\"\r\n        \"ai-grid ai-grid ai-grid\";\r\n        background: white;\r\n        }\r\n    #ai-grid-container {\r\n        grid-area: ai-grid;\r\n        background: white;\r\n        order: 3;\r\n    }\r\n    #player-grid-container {\r\n        grid-area: player-grid;\r\n    }\r\n    #ui-controls {\r\n        grid-area: ui-controls;\r\n        background: $container-color;\r\n    }\r\n} \r\n\r\n/*  -------------------   SHIP GUESS FORM   ---------------------*/\r\n\r\n#guess-form {\r\n    @include flexCenter(column);\r\n    font-size: 1.5em;\r\n    font-family: sans-serif;\r\n    margin-top: 8em;\r\n    margin-bottom: 2em;\r\n    text-align: center;\r\n    input {\r\n        font-size: 1.5em;\r\n        box-shadow: 0em 0em 0.2em 0em #949494;\r\n        width: 50%;\r\n        border-style: none;\r\n        text-align: center;\r\n        border-radius: 0.3em;\r\n    }\r\n    button {\r\n        font-size: 1.5em;\r\n        @extend .ship-select-btn;\r\n        width: 80%;\r\n    }\r\n    button:hover:enabled {\r\n        @extend .ship-select-btn:hover;\r\n    }\r\n} \r\n\r\n@media screen and (max-width: 1200px){\r\n    #guess-form {\r\n        margin-top: 3em;\r\n        margin-bottom: 1em;\r\n    }  \r\n}\r\n\r\n@media screen and (max-width: 450px){\r\n    #guess-form {\r\n        margin: 0;\r\n    }\r\n}\r\n\r\n/*  ---------------------   SHIP SETUP  ------------------  */\r\n\r\n.ship-select-btn {\r\n    width: 100%;\r\n    padding: 0.3em;\r\n    margin: 0.4em;\r\n    font-size: 1.2em;\r\n    box-shadow: 0em 0em 0.2em 0em #949494;\r\n    background:#f7ffff;\r\n    border-radius: 0.5em;\r\n    border: none;\r\n}\r\n\r\n.ship-select-btn:hover:enabled {\r\n    background: brown;\r\n    color: white;\r\n    transition: 0.4s ease;\r\n}\r\n\r\n#ship-setup-container {\r\n    width: 100%;\r\n    position: absolute;\r\n    top: 20%;\r\n    font-size: 1.5em;\r\n    text-align: center;\r\n    @include flexCenter(column);\r\n    #ship-select-btns {\r\n        @include flexCenter(column);\r\n    }\r\n    #toggle-ship-direction {\r\n        @extend .ship-select-btn;\r\n        width: auto;\r\n    }\r\n}\r\n\r\n#submit-btn {\r\n    width: 80% !important;\r\n}\r\n\r\n@media screen and (max-width: 1200px){\r\n    #ship-setup-container {\r\n        font-size: 1em;\r\n    }\r\n} \r\n\r\n@media screen and (max-width: 900px){\r\n    #ship-setup-container {\r\n        font-size: 0.8em;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 600px){\r\