ius: 50%;\r\n    box-shadow: 0.1em 0 0.3em 0.05em white;\r\n}\r\n","/*  --------------------- HIT ANIMATION -------------- */\r\n\r\n.cannonball {\r\n    background: $cannonball-color;\r\n    border-radius: 50%;\r\n    animation: drop 2s ease-in;\r\n    z-index: 1;\r\n    position: absolute;\r\n}\r\n\r\n@keyframes drop {\r\n    from {\r\n        height: 50%;\r\n        width: 50%;\r\n    }\r\n    to {\r\n        height: 10%;\r\n        width: 10%;\r\n    }\r\n}\r\n\r\n.flame {\r\n    bottom: 20%;\r\n    position:absolute;\r\n    border-bottom-right-radius: 50%;\r\n    border-bottom-left-radius: 50%;\r\n    border-top-left-radius: 50%;\r\n    transform:rotate(-45deg) scale(1.5,1.5);\r\n    z-index: 1;\r\n}\r\n  \r\n.red {\r\n    width: 40%;\r\n    height: 40%;\r\n    background:OrangeRed;\r\n    box-shadow: 0px 0px 0.25em 0.125em OrangeRed;\r\n}\r\n\r\n.orange {\r\n  width: 30%;\r\n  height: 30%;\r\n  background:orange;\r\n  box-shadow: 0px 0px 0.5em 0.125em orange;\r\n}\r\n\r\n.yellow {\r\n    width: 20%;\r\n    height: 20%;\r\n    background:gold;\r\n    box-shadow: 0px 0px 0.5em 0.125em gold;\r\n}\r\n\r\n.white {\r\n    width: 10%;\r\n    height: 10%;\r\n    background:white;\r\n    box-shadow: 0px 0px 0.4em 0.125em white;\r\n}","//  ------------------------  WAVES STYLING  ------------------------------------ //\r\n\r\n@mixin wave {\r\n    background: lightgray;\r\n    height: 5%;\r\n    width: 10%;\r\n    position: absolute;\r\n    z-index: 0;\r\n}\r\n\r\n.wave-1-phase1 {\r\n    @include wave;\r\n    transform: rotate(15deg);\r\n    top: 75%;\r\n    right: 60%;\r\n    border-bottom-left-radius: 50%; \r\n}\r\n\r\n.wave-2-phase1 {\r\n    @include wave;\r\n    transform: rotate(-15deg);\r\n    top: 75%;\r\n    right: 70%;\r\n    border-bottom-right-radius: 50%;\r\n}\r\n\r\n.wave-3-phase1 {\r\n    @include wave;\r\n    transform: rotate(-15deg);\r\n    bottom: 0%;\r\n    left: 5%;\r\n    border-bottom-right-radius: 50%;\r\n}\r\n.wave-4-phase1 {\r\n    @include wave;